<template>
  <div id="merchants">
    <div class="top">
      <div>
        <div class="tl">
          <span style="line-height:20px;font-size:18px">{{data.name}}</span>
          <p>
            <van-rate
              v-model="data.score"
              allow-half
              void-icon="star"
              :size="14"
              void-color="#d5d6da"
              color="#fe9900"
              gutter="6px"
              style="vertical-align:bottom;margin-right:10px"
            />
            <span style="font-size:12px;margin-right:10px">({{data.ratingCount}})</span>
            <span style="font-size:12px">月售{{data.sellCount}}单</span>
          </p>
        </div>
        <div class="tr" style="font-size:24px">
          <van-icon name="like-o" />
          <p style="font-size:12px">已收藏</p>
        </div>
      </div>
      <div>
        <div>
          <span style="font-size:14px;color:#a6a8ac">起送价</span>
          <p style="margin-top:4px;font-size:24px;color:#36434e">{{data.minPrice}}<span style="font-size:12px">元</span></p>
        </div>
        <div style="border-left:1px solid #eee;border-right:1px solid #eee">
          <span style="font-size:14px;color:#a6a8ac">商家配送</span>
          <p style="margin-top:4px;font-size:24px;color:#36434e">{{data.deliveryPrice}}<span style="font-size:12px">元</span></p>
        </div>
        <div>
          <span style="font-size:14px;color:#a6a8ac">平均配送时间</span>
          <p style="margin-top:4px;font-size:24px;color:#36434e">{{data.deliveryTime}}<span style="font-size:12px">分钟</span></p>
        </div>
      </div>
    </div>
    <div class="middle"></div>
    <div class="active">
      <h3 style="line-height:40px">公告与活动</h3>
      <div>
        <p style="margin-bottom:10px;line-height:24px">{{data.bulletin}}</p>
      </div>
      <div style="height:40px;border-top:1px solid #eee;box-sizing:border-box;line-height:40px;color:#32363c;font-size:14px;padding:0 10px" v-for="(v,i) in data.supports" :key="i">{{data.supports[i].description}}</div>
    </div>
    <div class="middle"></div>
    <div class="imgs">
      <h3 style="line-height:40px">商家实景</h3>
      <div>
        <img v-for="(j,k) in data.pics" :key="k" :src="data.pics[k]" >
      </div>
    </div>
    <div class="middle"></div>
    <div class="info">
      <h3 style="line-height:40px">商家信息</h3>
      <div v-for="(v,i) in data.infos" :key="i">{{data.infos[i]}}</div>
    </div>
  </div>
</template>

<script>
import { getInfo } from "../api/api";
export default {
  data() {
    return {
      data : {},
      value : 4
    };
  },
  methods: {
    async info(){
      let d = await getInfo();
      this.data = d;
    }
  },
  created(){
    this.info();
  }
};
</script>

<style>
#merchants {
  margin-bottom: 60px;
}
#merchants > .top {
  box-sizing: border-box;
  padding: 0 20px;
  height: 160px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
#merchants > .top > div {
  height: 80px;
  box-sizing: border-box;
  padding: 20px 0;
}
#merchants > .top > div:nth-child(1) {
  border-bottom: 1px solid #eee;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
#merchants > .top .tl {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
#merchants > .top .tr{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
#merchants > .top > div:nth-child(2) {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
#merchants > .top > div:nth-child(2)>div{
  flex: 1;
  text-align: center;
}
#merchants > .middle {
  height: 20px;
  background: #f4f5f7;
  border-top: 1px solid #ddd;
  border-bottom: 1px solid #ddd;
}
#merchants >.active{
  padding: 0 20px;
  box-sizing: border-box;
}
#merchants >.active>div:nth-child(2){
  box-sizing: border-box;
  padding: 0 10px;
  font-size: 14px;
  color: #ed2f2e;
}
#merchants >.imgs{
  padding: 0  0 0 20px;
  box-sizing: border-box;
  margin-bottom: 10px;
}
#merchants >.imgs>div{
  display: flex;
  overflow: scroll;
}
#merchants >.imgs>div>img{
  width: 120px;
  height: 90px;
  margin-right: 10px;
}
#merchants >.info{
  box-sizing: border-box;
  padding: 0 20px;
}
#merchants >.info>div{
  box-sizing: border-box;
  border-top: 1px solid #eee;
  padding: 0 10px;
  line-height: 40px;
  font-size: 14px;
  color: #8c8d8e;
}
</style>
